<template>
  <div>
    <Navbar>
      <template v-slot:left>
        <div class="icon"><i class="iconfont icon-jiantou"></i></div>
      </template>
      <template v-slot:default>
        购物车(<span>{{ $store.state.cartCount }}</span
        >)
      </template>
      <template v-slot:right> </template>
    </Navbar>
    <div class="cart-box">
      <div class="cart-body">
        <van-checkbox-group
          @change="groupCheck"
          v-model="result"
          ref="checkboxGroup"
        >
          <van-swipe-cell v-for="(item, index) in list" :key="index">
            <van-checkbox :name="item.id" style="margin-left: 20px">
              <van-card
                :num="item.goods.stock"
                :price="item.goods.price + '.00'"
                :desc="item.goods.updated_at"
                :title="item.goods.title"
                :thumb="item.goods.cover_url"
              >
                <template #tags>
                  <van-tag plain type="danger">标签</van-tag>
                  <van-tag plain type="danger">标签</van-tag>
                </template>
              </van-card>
            </van-checkbox>
            <van-stepper
              @change="onChange"
              :name="item.id"
              v-model="item.num"
              min="1"
              :max="item.goods.stock"
              integer
              async-change
            />
            <template #right>
              <van-button
                square
                text="删除"
                type="danger"
                class="delete-button"
                @click="deleteGoods(item.id)"
              />
            </template>
          </van-swipe-cell>
        </van-checkbox-group>
        <van-submit-bar :price="total*100" @submit="onsubmit" button-text="提交订单">
          <van-checkbox ref="toggle" @click="checkAll" v-model="checkboxResult"
            >全选</van-checkbox
          >
          <template #tip>
            你的收货地址不支持同城送,
            <span>修改地址</span>
          </template>
        </van-submit-bar>
        <van-empty v-if="!list.length" description="购物车空空如也">
          <van-button @click="goto" round type="danger" class="bottom-button"
            >立即购物</van-button
          >
        </van-empty>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "components/common/navbar/Navbar";
import { getCart, deleteCartItem, checkCart, modifyCart } from "network/cart";
import { ref, onMounted, reactive, toRefs, computed } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { Toast } from "vant";

export default {
  name: "Cart",
  setup() {
    const toggle = ref(null);
    const result = ref([]);
    const checkboxGroup = ref(null);
    const router = useRouter();
    const store = useStore();
    let temp = ref(false);

    const goto = () => {
      router.push({ path: "/category" });
    };
    const state = reactive({
      list: [],
      result: [],
      checkboxResult: true,
    });

    const deleteGoods = (id) => {
      deleteCartItem(id).then(res => {
        // 重新初始化
        init();
        store.dispatch('updataCart');
      })
    }

    const total = computed(() => {
      let sum = 0;
      state.list.filter(item=>state.result.includes(item.id)).forEach(item => {
        sum += parseInt(item.num) * parseFloat(item.goods.price);
      });
      return sum;
    })

    // 创建订单
    const onsubmit = () => {
      if(state.result.length == 0) {
        Toast.fail('请选择商品')
        return;
      }else{
        router.push({path:'/orders'});
      }
    }

    // 初始化购物车数据
    const init = () => {
      Toast.loading({ message: "加载中...", forbidClick: true });
      getCart("include=goods").then((res) => {
        // console.log(res.data);
        state.list = res.data;
        state.result = res.data
          .filter((n) => n.is_checked == 1)
          .map((item) => item.id);
        store.state.cartCount = res.data.length;
        temp = res.data.length;
        Toast.clear();
      });
    };

    onMounted(() => {
      init();
    });

    // 异步改变购物车
    const onChange = (value, detail) => {
      Toast.loading({ forbidClick: true });
      modifyCart(detail.name, { num: value }).then((res) => {
        // 改变list数据中的num重新计算
        state.list.forEach((item) => {
          if (item.id == detail.name) {
            item.num = value;
          }
        });
        Toast.clear();
      });
    };

    // 复选框改变
    const groupCheck = (result) => {
      if (result.length == store.state.cartCount) {
        toggle.value.toggle(true);
      } else if (result.length != temp) {
        toggle.value.toggle(false);
      }

      // console.log(result);
      // 同步服务器
      checkCart({ cart_ids: result });
    };

    const checkAll = () => {
      if (state.checkboxResult) {
        state.checkboxResult = true;
        checkboxGroup.value.toggleAll(true);
        toggle.value.toggle(true);
      } else {
        state.checkboxResult = false;
        checkboxGroup.value.toggleAll(false);
        toggle.value.toggle(false);
      }
    };

    return {
      result,
      checkAll,
      checkboxGroup,
      ...toRefs(state),
      goto,
      onChange,
      groupCheck,
      toggle,
      deleteGoods,
      total,
      onsubmit,
    };
  },
  components: {
    Navbar,
  },
};
</script>

<style >
.cart-box {
  margin-top: 45px;
}
.goods-card {
  margin: 0;
  background-color: white;
}
.van-checkbox__label {
  width: 100%;
}
.delete-button {
  height: 100%;
}
.van-card__num {
  font-size: 10px;
  margin-right: 30px;
  float: right;
}
.van-button-checked {
  width: 50px;
  float: right;
}
.van-stepper {
  float: right;
  margin-right: 20px;
  margin-bottom: 10px;
}
.van-checkbox-group {
  background-color: #fafafa;
}
.num {
  float: right;
  width: 40px;
}
.van-submit-bar {
  margin-bottom: 50px;
  z-index: 1;
}
</style>